# Hero Section
--------------------------------------------------------------*/
#hero {
  width: 100%;
  height: 100vh;
  /* background: url("../pexels-alex-conchillos-3648850.jpg") top right no-repeat;
  background-size: cover; */
  position: relative;
}

@media (min-width: 992px) {
  #hero {
    padding-left: 160px;
  }
}

#hero:before {
  content: "";
  
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

#hero h1 {
  margin: 0;
  font-size: 72px;
  font-weight: 700;
  line-height: 56px;
  color: #f1d4d4;
}

#hero p {
  color: #45505b;
  margin: 15px 0 0 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
}

#hero p span {
  color: #c060a1;
  letter-spacing: 1px;
}

#hero .social-links {
  margin-top: 30px;
}

#hero .social-links a {
  font-size: 24px;
  display: inline-block;
  color: #f1d4d4;
  line-height: 1;
  margin-right: 20px;
  transition: 0.3s;
  text-decoration: none;
}

#hero .social-links a:hover {
  color: #6a097d;
}

@media (max-width: 992px) {
  #hero {
    text-align: center;
  }
  #hero h1 {
    font-size: 46px;
    line-height: 36px;
  }
  #hero p {
    margin-top: 20px;
    font-size: 26px;
    line-height: 24px;
  }
}
